.header__link {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;

  color: #fff;
  text-decoration: none;

  transition: opacity .3s;
}

.header__link:hover {
  opacity: .6;
}

@media screen and (max-width: 767px) {
  .header__link {
    font-size: 14px;
    margin-right: 30px;
  }
}